import React, { useState, useEffect } from 'react';
import { Link, useParams, Redirect } from 'react-router-dom';
import { getDetail, url } from '../request/request';
import Loading from '../components/Loading';
import Comments from './Comments';

const Detail = () => {
	const [ data, setData ] = useState({});
	const [ isLoading, setLoading ] = useState(true);
	const { episodeId } = useParams();

	useEffect(
		() => {
			setLoading(true);
			let epData = localStorage.getItem(episodeId);
			if (epData) {
				epData = JSON.parse(epData);
				setData(epData);
				setLoading(false);
			} else {
				getDetail(episodeId)
					.then((res) => {
						setData(res);
						setLoading(false);
						console.log(res);
						epData = res;
						document.title = `Watch ${res.title} for free`;
					})
					.catch((err) => {
						setLoading(false);
						console.log(err);
					});
			}
			return () => {
				if (epData) localStorage.setItem(episodeId, JSON.stringify(epData));
			};
		},
		[ episodeId ]
	);

	if (isLoading) {
		return <Loading />;
	}

	if (!isLoading && !data.title) {
		return <Redirect to="/" />;
	}

	return (
		<div>
			<div className="ui container">
				<div>
					<Link to={data.all}>All Episodes</Link>
					<span> | </span>
					<Link to={data.prev}>Previous</Link>
					<span> | </span>
					<Link to={data.next}>Next</Link>
				</div>
				<h3 className="ui header mb-4">{data.title}</h3>

				<div className="video-container">
					{data.video && (
						<video
							src={`${url}/api/video?url=${data.video}&embed=${data.embed}`}
							width="100%"
							height="100%"
							controls
						/>
					)}
				</div>
				{episodeId && !isLoading && <Comments parentId={episodeId} />}
			</div>
		</div>
	);
};

export default Detail;
